.quotes {
    padding-top: 109px;
    padding-bottom: 108px;
    background: linear-gradient(180deg, #EAF0FF 0%, #F6F9FF 100%);
    border-top: 1px solid #D8E3FE;
    border-bottom: 1px solid #D8E3FE;

    &__container {
        max-width:918px;
        width: 100%;
        padding: 0 15px;
    }

    &__wrapper {
        @media screen and(max-width: 785px) {
            flex-direction: column;
            align-items: center;
            row-gap: 30px;
        }
    }

    &__content {
        max-width: 333px;
        width: 100%;
    }

    &__subtitle {
        font-family: "Roboto-Medium", sans-serif;
        font-weight: 500;
        font-size: 10px;
        line-height: 12px;
        letter-spacing: 0.1em;
        color: #3D4F9F;
        margin-bottom: 31px;
    }

    &__title {
        font-family: "Quicksand-Medium", sans-serif;
        font-weight: 500;
        font-size: 36px;
        line-height: 45px;
        color: #3D4F9F;
        margin-bottom: 14px;
    }

    &__text {
        max-width: 323px;
        font-family: "OpenSans-SemiBold", sans-serif;
        font-weight: 600;
        font-size: 12px;
        line-height: 23px;
        letter-spacing: 0.03em;
        color: #A4ABC8;
    }

    &__carousel {
        max-width: 423px;
        width: 100%;
    }

    &__carousel-item {
        width: 100%;
    }

    &__carousel-item-text {
        max-width: 423px;
        padding-top: 41px;
        padding-bottom: 53px;
        font-family: "Roboto-Regular", sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 0.03em;
        color: #676F95;
        background: #FFFFFF;
        border: 1px solid #D8E3FE;
        box-shadow: 0px 5px 50px #E0E5F3;
    }

    &__carousel-item-quote {
        max-width: 313px;
        padding: 0 15px;
    }

    &__carousel-item-img {
        width: 62px;
        height: 62px;
        object-fit: contain;
        transform: translateY(-31px);
        margin-bottom: 27px;
    }
}

